/** Media Queries **/
.contenedor-inicio {
  width: 90%;
  max-width: 1000px;
  margin: 0 auto;
}

.contenedor {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
}

.webp .principal {
  background-image: url(../../../build/img/imagen-fondo.webp);
}

.no-webp .principal {
  background-image: url(../../../src/img/imagen-fondo.jpeg);
}

.principal {
  background-position-x: center;
}
@media (min-width: 1024px) {
  .principal {
    background-size: cover;
  }
}

.secundario {
  background-color: rgba(130, 46, 129, 0.1);
}

.ocultar {
  visibility: hidden;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
  font-family: "Rubik", sans-serif;
}

*, *:before, *:after {
  box-sizing: inherit;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.header {
  margin-top: 2rem;
  margin-bottom: 2rem;
  border-bottom: 2px #9FC552 solid;
}
.header .contenedor-logo {
  text-align: center;
}
.header .contenedor-logo .logo {
  width: 20rem;
}
@media (min-width: 768px) {
  .header {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  .header .contenedor-logo {
    flex-shrink: 0;
  }
}

@media (min-width: 768px) {
  .menu-ham--icono {
    display: none;
  }
}
.navegacion {
  list-style: none;
  padding: 0;
}
.navegacion li {
  padding: 1rem;
  margin: 1px;
}
.navegacion li a {
  font-size: 2rem;
  color: #822E81;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  .navegacion li a:hover {
    color: #624F41;
  }
}
@media (max-width: 768px) {
  .navegacion {
    height: 0;
    overflow: hidden;
    transition: all 0.5s;
  }
  .navegacion li {
    background-color: #9FC552;
  }
  .navegacion li:hover {
    background-color: #3C7135;
    cursor: pointer;
  }
  .navegacion li:hover a {
    color: #fff;
  }
}

.header-principal {
  border-bottom: none;
}
.header-principal .contenedor-logo .logo {
  width: 30rem;
}
.header-principal .navegacion {
  overflow: visible;
  text-align: center;
}
.header-principal .navegacion li {
  background-color: transparent;
  padding: 0;
}
.header-principal .navegacion li a:hover {
  color: #624F41;
}
@media (min-width: 768px) {
  .header-principal .contenedor-logo {
    flex-shrink: 0;
  }
  .header-principal .navegacion li {
    padding: 1rem;
  }
}

.footer-principal {
  position: fixed;
  bottom: 0;
  width: 100%;
}

.div-footer {
  margin-bottom: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .div-footer {
    flex-direction: row;
  }
}
.div-footer a {
  display: flex;
  align-items: center;
}
@media (min-width: 768px) {
  .div-footer a {
    margin-right: 2rem;
  }
}
.div-footer a p {
  color: #fff;
}

.footer {
  text-align: center;
}

.nosotros {
  margin-bottom: 2rem;
}
.nosotros .nosotros--img {
  text-align: center;
}
.nosotros .nosotros--img img {
  width: 50rem;
}
.nosotros .nosotros--info h2 {
  font-size: 4rem;
  color: #3C7135;
  margin: 0;
  margin-left: 2rem;
}
.nosotros .nosotros--info p {
  font-size: 1.7rem;
  padding: 2rem;
  text-align: center;
  line-height: 3rem;
}
@media (min-width: 768px) {
  .nosotros {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .nosotros .nosotros--info p {
    text-align: left;
  }
}

.mv {
  padding: 0.5rem 0;
  background-color: #9FC552;
  width: 100%;
  margin-bottom: 2rem;
}

@media (min-width: 768px) {
  .contenedor-mv {
    display: flex;
  }
}
.contenedor-mv .mv--info {
  padding: 1rem 2rem;
  margin: 2rem 1rem;
  border-radius: 1rem;
  background-color: rgba(255, 255, 255, 0.2);
}
.contenedor-mv .mv--info h3 {
  font-size: 2rem;
  text-align: center;
}
.contenedor-mv .mv--info p {
  font-size: 1.5rem;
  text-align: center;
  line-height: 2.5rem;
}

.body-actividades {
  background-image: linear-gradient(to bottom, transparent, rgba(159, 197, 82, 0.6));
}

.act-parrafo {
  font-size: 2rem;
  color: #3C7135;
  margin: 4rem 0;
  text-align: center;
}
@media (min-width: 768px) {
  .act-parrafo {
    text-align: left;
  }
}

.contenedor-actividades {
  margin-top: 3rem;
  margin-bottom: 3rem;
}
.contenedor-actividades .actividad {
  text-align: center;
}
.contenedor-actividades .actividad .actividad--imagen {
  width: 30rem;
  border-radius: 50%;
}
.contenedor-actividades .actividad:hover {
  cursor: pointer;
}
@media (max-width: 768px) {
  .contenedor-actividades .actividad {
    margin-bottom: 4rem;
  }
}
@media (min-width: 768px) {
  .contenedor-actividades {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .contenedor-actividades {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;
  overflow-y: auto;
}
.overlay .info-actividad {
  background-color: #fff;
  max-width: 80rem;
  padding: 2rem;
  margin: 2rem;
  border-radius: 2rem;
  border: 0.5rem #624F41 solid;
  text-align: center;
}
.overlay .info-actividad h2 {
  color: #3C7135;
  font-size: 2.5rem;
}
.overlay .info-actividad p {
  text-align: justify;
  font-size: 1.7rem;
  line-height: 2.8rem;
}
.overlay .info-actividad img {
  width: 40rem;
}
.overlay .info-actividad span {
  display: block;
  color: red;
  font-size: 1.5rem;
  margin-top: 1rem;
}
.overlay .info-actividad button {
  display: block;
  margin: auto;
  margin-top: 1rem;
  padding: 0.5rem 1rem;
  font-size: 1.5rem;
  background-color: #9FC552;
  border: none;
  cursor: pointer;
}
.overlay .info-actividad button:hover {
  background-color: rgba(159, 197, 82, 0.9);
}

.seccion-galeria {
  margin-bottom: 2rem;
}
@media (min-width: 480px) {
  .seccion-galeria {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.seccion-galeria h2 {
  font-size: 5rem;
  color: #3C7135;
  font-weight: 500;
}
@media (min-width: 480px) {
  .seccion-galeria h2 {
    margin-top: 10rem;
  }
}
.seccion-galeria span {
  display: block;
  color: #624F41;
  font-weight: bold;
}

.galeria {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3, 1fr);
}
.galeria .img-individual {
  width: 100%;
  height: 15rem;
  overflow: hidden;
}
.galeria .img-individual img {
  max-width: 200px;
}
@media (min-width: 480px) {
  .galeria .img-individual {
    min-width: 15rem;
  }
}

.contenedor-foto {
  position: relative;
  height: 60rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.contenedor-foto img {
  max-height: 100%;
}
.contenedor-foto .cerrar-foto {
  align-self: flex-start;
  opacity: 0.5;
  font-size: 1.5rem;
  position: absolute;
  top: 1px;
}
.contenedor-foto button {
  background-color: transparent;
  border: none;
}
.contenedor-foto button:hover {
  cursor: pointer;
}

.botones-galeria {
  width: 100%;
  display: flex;
  justify-content: space-between;
  position: absolute;
  top: 50%;
}
.botones-galeria button {
  color: #fff;
  font-size: 3rem;
}

.voluntariados {
  display: grid;
  gap: 3rem;
  grid-template-columns: repeat(1, 1fr);
}
.voluntariados .vol-foto2 {
  grid-row: 4/5;
}
@media (min-width: 480px) {
  .voluntariados {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .voluntariados .vol-foto2 {
    grid-row: auto;
  }
  .voluntariados img {
    min-width: 40rem;
  }
}

.vol-info h2 {
  font-size: 2.5rem;
}
.vol-info p {
  font-size: 1.8rem;
  line-height: 1.5;
}

.instalaciones p {
  font-size: 2rem;
}
@media (min-width: 480px) {
  .instalaciones .instalaciones-img {
    display: flex;
  }
  .instalaciones .instalaciones-img picture {
    margin: 1rem;
  }
}